@use "@app/bancor-components/Element/Form";
@use "@app/bancor-components/Element/Form/InputCheckbox";
@use "@app/bancor-components/Element/Button";
@use "@app/bancor-components/Element/Text";
@use "@app/bancor-components/Style/Color";
@use "@app/bancor-components/Style/Shadow";
@use "@app/bancor-components/Style/Border";
@use "@app/bancor-components/Style/Font";
@use "@app/bancor-components/Style/Layout";
@use "@app/bancor-components/Style/Layout/Flex";
@use "@app/bancor-components/Style/Layout/Margin";
@use "@app/bancor-components/Style/Layout/Padding";

.register {
    > header {
        & #{Text.$selector} {
            @include Margin.set($top: alpha, $bottom: beta);
            @include Font.set($size: delta, $weight: bold);
        }
    }

    .personalData {
        > li {
            @include Flex.set($justify: between);
            @include Margin.set($bottom: alpha);

            #{Text.$selector} {
                @include Color.set($fg: "gray-dark");
            }

            #{Text.$selector}:last-child {
                @include Font.set($weight: bold);
            }
        }
    }

    .termsText {
        @include Color.set($fg: "gray-dark");

        h1 {
            @include Font.set($size: iota, $weight: bold, $family: "heading");
        }

        h2 {
            @include Font.set($size: delta, $weight: bold, $family: "heading");
        }

        ol {
            @include Margin.set($preset: gamma);
            list-style: revert;

            li {
                @include Color.set($fg: "gray-dark");
                @include Font.set($size: gamma, $weight: default, $family: "default");

                a {
                    @include Color.set($fg: "primary");
                    @include Font.set($weight: bold);
                }
            }
        }
    }

    #{Form.$selector} {
        #{InputCheckbox.$selector} {
            @include Margin.set($top: beta, $bottom: beta);

            .termsAndConditions {
                @include Flex.set($justify: start);

                & > #{Text.$selector} {
                    @include Color.set($fg: "gray-dark");
                    @include Font.set($transform: caps);
                }

                #{Button.$selector} {
                    @include Button.asUnderlineLink;
                }
            }
        }

        .alert {
            @include Flex.set($align: center);

            #{Text.$selector} {
                @include Color.set($fg: "gray-dark");
            }

            .alertIcon {
                @include Flex.set($shrink: none);
                @include Margin.set($right: alpha);
            }
        }

        .verificationCode {
            #{Text.$selector}.verificationCodeSubtitle {
                @include Color.set($fg: "gray-dark");
            }

            #{Button.$selector} {
                @include Margin.set($top: beta, $bottom: beta);
            }

            #{Text.$selector}.codeSent {
                @include Color.set($fg: "primary");
                @include Font.set($weight: bold);
                @include Margin.set($bottom: beta);
            }
        }

        .verificationCodeLottie {
            @include Flex.set($justify: center);
            @include Margin.set($bottom: beta);
        }

        .verificationCodeFooter {
            width: Layout.get-max-width(alpha);
            #{Button.$selector}:last-child {
                @include Margin.set($top: beta);
            }
        }

        > footer {
            #{Button.$selector} {
                @include Margin.set($top: gamma, $bottom: gamma);
            }
        }
    }
}
